import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ToastService } from 'src/app/core/services/toast/toast.service';
import { ApicallService } from 'src/app/core/services/apicall/apicall.service'; 
import { GlobalService } from 'src/app/core/services/global/global.service'; 
@Component({
  selector: 'app-login',
  templateUrl: './login.page.html',
  styleUrls: ['./login.page.scss'],
})
export class LoginPage implements OnInit {
public user:any= { login_id:'', password:'' , status:'active'}
  constructor( private router:Router , public toast : ToastService, public apicall : ApicallService, public global : GlobalService) { }

  ngOnInit() {
  }
  login(){
    console.log(this.user)
    this.apicall.api_login(this.user);
    this.user= { login_id:'', password:'', status:'active'}
  }

  ShowHidePassword(ele, eye) {
    if (ele.type === 'password') {
      ele.type = 'text';
      eye.name = 'eye'
    }
    else {
      ele.type = 'password';
      eye.name = 'eye-off'
    }
  }

}
